import { Button, ButtonGroup } from '@mui/material';

const Options = (props) => {
    const goal = props.goal;
    const handleClick = (event) => {
        props.updateGoal(event.currentTarget.name);
    };

    return (
        <ButtonGroup size='small' variant='contained' fullWidth>
            {goal !== 'final-amount' ? (
                <Button name='final-amount' onClick={handleClick}>
                    Final Amount
                </Button>
            ) : (
                <Button name='final-amount' disabled onClick={handleClick}>
                    Final Amount
                </Button>
            )}
            {goal !== 'duration' ? (
                <Button name='duration' onClick={handleClick}>
                    Duration
                </Button>
            ) : (
                <Button name='duration' disabled onClick={handleClick}>
                    Duration
                </Button>
            )}
        </ButtonGroup>
    );
};

export default Options;
